<template>
  <div class="info-form">
    <Form ref="formValidate" :model="formValidate" :label-width="80" :rules="ruleValidate">
      <FormItem label="" prop="phone">
          <i-input v-model="formValidate.phone" clearable size="large"  placeholder=""></i-input>
      </FormItem>
      <FormItem label="" prop="checkNum">
          <i-input v-model="formValidate.checkNum" size="large"  placeholder="">
            <Button slot="append" @click="getcheckNum"></Button>
          </i-input>
      </FormItem>
      <Button type="error" size="large" long @click="handleSubmit('formValidate')"></Button>
    </Form>
  </div>
</template>

<script>
import store from '@/vuex/store';
import { mapMutations } from 'vuex';
export default {
  name: 'CheckPhone',
  data () {
    return {
      formValidate: {
        phone: '',
        checkNum: ''
      },
      ruleValidate: {
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', pattern: /^1[3|4|5|7|8][0-9]{9}$/, message: '', trigger: 'blur' }
        ],
        checkNum: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', min: 4, max: 4, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    ...mapMutations(['SET_SIGN_UP_SETP']),
    getcheckNum () {
      if (this.formValidate.phone.length === 11) {
        this.$Message.success({
          content: ': 1234',
          duration: 6,
          closable: true
        });
      } else {
        this.$Message.error({
          content: '',
          duration: 6,
          closable: true
        });
      }
    },
    handleSubmit (name) { // 
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$router.push({ path: '/SignUp/inputInfo', query: { phone: this.formValidate.phone } });
          this.SET_SIGN_UP_SETP(1);
        } else {
          this.$Message.error({
            content: '',
            duration: 6,
            closable: true
          });
        }
      });
    }
  },
  store
};
</script>

<style scoped>
.info-form {
  width: 90% !important;
}
</style>



